<template>
  <div class="ditty">
    <div class="title-row">
      <h4 class="title">榆林小曲曲目欣赏</h4>
    </div>
    <div class="kind">
      <div class="kind-row">
        <div class="kind-col">
          <div class="kind-col-l title-wrap" @click="handlePlay('08_sbmg')">
            <span class="kind-title">陕北民歌</span>
          </div>
        </div>
        <div class="kind-col">
          <div class="kind-top">
            <div class="kind-top-c title-wrap" @click="handlePlay('08_bysdjyy')">
              <span class="kind-title">白云山道教音乐</span>
            </div>
          </div>
          <div class="kind-bottom">
            <div class="kind-bottom-c title-wrap" @click="handlePlay('08_smsn')">
              <span class="kind-title">绥米唢呐</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="back-row">
      <img class="back-btn" :src="backBtn" alt="" @click="handleBack" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      backBtn: require("@/assets/songList/back_btn@2x.png")
    };
  },
  methods: {
    // 返回
    handleBack() {
      this.$router.push("/tradition");
    },
    // 点击播放
    handlePlay(name) {
      if(!name){
        return;
      }
      this.$router.push({
        path: "/player",
        query: {
          src: name
        }
      });
    }
  }
};
</script>

